/**
 * North Arrow
 */
import React from 'react'
import { Glyphicon } from 'react-bootstrap'

export default class NorthArrow extends React.Component {

  static defaultProps = {
    zIndex: 15,
    bottom: 150,
    right: 10,
    width: 60,
    height: 60
  }

  constructor(props) {
    super(props)

    this.state = {
      bearing: 0,
      hover: false,
      active: false
    }
    this.handleClick = this.handleClick.bind(this)
    this.handleMouseEnter = this.handleMouseEnter.bind(this)
    this.handleMouseLeave = this.handleMouseLeave.bind(this)
  }

  componentDidMount() {
    map.on('rotate', this.getBearing.bind(this))
    this.setState({ active: true })
  }

  getBearing(e) {
    this.setState({ bearing: map.getBearing() })
  }

  handleClick() {
    let bearingSwitch = {
      0: 90,
      90: 180,
      180: 270,
      270: 0
    }
    map.flyTo({ bearing: bearingSwitch[this.state.bearing] || 0 })
  }

  handleMouseEnter() {
    this.setState({ hover: true })
  }

  handleMouseLeave() {
    this.setState({ hover: false })
  }

  render() {
    const styles = {
      container : {
        position : 'absolute',
        cursor: `pointer`,
        top: this.props.top,
        bottom: this.props.bottom,
        right: this.props.right,
        left: this.props.left,
        zIndex: this.props.zIndex,
        width: this.props.width,
        height: this.props.height
      },
      northArrow : {
        position : 'absolute',
        width: this.props.width,
        height: this.props.height,
        transform: `rotate(${ this.state.bearing }deg)`,
        WebkitFilter: (!this.state.bearing) ? `grayscale(1)` : ``,
        backgroundImage: 'url(data:image/svg+xml;base64,)'
      },
      background : {
        position : 'absolute',
        width: this.props.width,
        height: this.props.height,
        transition: 'all 0.3s',
        WebkitFilter: (this.state.hover) ? `invert(0.2)` : ``,
        backgroundImage: 'url(data:image/svg+xml;base64,)'
      }
    }
    return (
      <div
        style={ styles.container }
        onClick={ this.handleClick }
        onMouseEnter={ this.handleMouseEnter }
        onMouseLeave={ this.handleMouseLeave }
        >
        <div style={ styles.background }></div>
        <div style={ styles.northArrow }></div>
      </div>
    )
  }
}



